export const dateFunction = () => {
    let date = new Date();
    let differenceDate = date.getTime() - new Date('2020-03-01').getTime();
    let numberOfMonths = Math.ceil(differenceDate / 1000 / 3600 / 24 / 30);
    let arrayOfData = []

    for (let i = 4; i < numberOfMonths + 3; i++) {
        let month, year = 2020;
        if (i > 12) {
            month = i - 12;
            year = 2021;
        } else {
            month = i;
        }

        arrayOfData.push(`${year}-${month < 10 ? `0${month}` : month}-01`);
    }
    arrayOfData.push(getCurrentDate());

    return arrayOfData;
}

export const getCurrentDate = () => {
    let date = new Date();
    let currentDate = `${date.getFullYear()}-${date.getUTCMonth() < 10? `0${date.getUTCMonth() + 1}` : date.getUTCMonth() + 1}-${date.getUTCDate() < 10
        ? `0${date.getUTCDate()}` : date.getUTCDate()}`;
    return currentDate;
}