import {countriesAPI, totalApi} from "../api/rapid-api";

const LIST_OF_COUNTRIES = 'LIST-OF-COUNTRIES'
const LATEST_TOTALS = 'LATEST-TOTALS'
const COUNTRY = 'COUNTRY'

let initState = {
    countriesList: [],
    latestTotals: {
        cases: {},
        deaths: {}
    },
    country: 'Ukraine',
    allWorld: 'all'
}

const countryReducer = (state = initState, action) => {
    switch (action.type) {
        case LIST_OF_COUNTRIES:
            return {...state, countriesList: action.countriesList}

        case LATEST_TOTALS:
            return {...state, latestTotals: action.latestTotals}

        case COUNTRY:
            return {...state, country: action.country}

        default:
            return state;
    }
}

export const listOfCountriesCreator = (countriesList) => ({type: LIST_OF_COUNTRIES, countriesList})
export const latestTotalsCreator = (latestTotals) => ({type: LATEST_TOTALS, latestTotals})
export const countryCreator = (country) => ({type: COUNTRY, country})

export const getListOfCountries = () => (dispatch) => {
    return countriesAPI.getListOfCountries().then(data => {
        dispatch (listOfCountriesCreator(data))
    })
}

export const getLatestTotals = (country, date) => (dispatch) => {
    return totalApi.getLatestTotals(country, date)
        .then(data => {dispatch (latestTotalsCreator(data.response[0]))
    }).catch(e => console.log(e.message))
}

export const setCountry = (country) => (dispatch) => {
    dispatch (countryCreator(country))
}

export default countryReducer;