import React from 'react';
import style from './DailyStatistic.module.css'

const DailyStatistic = (props) => {
    return(
        <div className={style.mainField}>
            <div className={style.field}>
                <h2>Daily Statistic {props.latestTotals.day}</h2>
                <div className={style.newCases}>New Cases: {props.latestTotals.cases.new}</div>
                <div className={style.newDeaths}>New Deaths: {props.latestTotals.deaths.new}</div>
            </div>
        </div>
    )
}

export default DailyStatistic;