import style from './Navbar.module.css'
import {NavLink} from "react-router-dom";
import React from "react";

const Navbar = () => {
    return (
        <nav className={style.nav}>
            <NavLink className={style.item} to='/world' activeClassName={style.activeLink}>All World</NavLink>

            <NavLink className={style.item} to='/countries' activeClassName={style.activeLink}>Countries</NavLink>
        </nav>
    )
}

export default Navbar;