import {totalApi} from "../api/rapid-api";

const GRAPHIC_DATA = 'GRAPHIC-DATA';
const SUCCESS = 'SUCCESS';
const CLEAR_DATA = 'CLEAR-DATA';

let initState = {
    data: [
        {
            name: 0,
            cases: 0,
            recovered: 0,
            deaths: 0
        }

    ],
    success: false
}

const graphicReducer = (state = initState, action) => {
    switch (action.type) {
        case GRAPHIC_DATA: {
            let array = state.data;

            for (let i = 0; i < action.data.length; i++) {
                if (action.data[i].results > 0) {
                    let graphicData = {
                        name: action.data[i].response[0].day,
                        cases: action.data[i].response[0].cases.total,
                        recovered: action.data[i].response[0].cases.recovered,
                        deaths: action.data[i].response[0].deaths.total
                    }
                    array.push(graphicData);
                }
            }
            return {...state, data: array}
        }

        case SUCCESS:
            return {...state, success: action.success}

        case CLEAR_DATA:
            return {...state, data: [{name: 0, cases: 0, recovered: 0, deaths: 0}]}

        default:
            return state;
    }
}

export const graphicDataCreator = (data) => ({type: GRAPHIC_DATA, data})
export const successCreator = (success) => ({type: SUCCESS, success})
export const clearDataCreator = () => ({type: CLEAR_DATA})

export const getGraphicData = (country, arrayOfDates) => (dispatch) => {
    let data = arrayOfDates.map(d => totalApi.getLatestTotals(country, d));
    let promise = Promise.all(data)
        .then(values => dispatch(graphicDataCreator(values)))
        .catch(dispatch(successCreator(false)));
    promise.then(dispatch(successCreator(true)))

}

export const clearGraphicData = () => (dispatch) => {
    dispatch (clearDataCreator())
}

export default graphicReducer;