import React from 'react';
import style from './CountriesStatistic.module.css'
import TotalStatistic from "../MainBody/TotalStatistics/TotalStatistic";
import DailyStatistic from "../MainBody/DailyStatistic/DailyStatistic";
import GraphStatisticContainer from "../MainBody/Graphics/GraphStatisticContainer";
import SearchCountriesField from "./Searching/SearchCountriesField";


const CountriesStatistic = (props) => {
    return(
        <div className={style.mainBody}>
            <SearchCountriesField countriesList={props.countries.countriesList} setCountry={props.setCountry}/>
            <div className={style.headText}><h2>{props.countries.country}</h2></div>
            <div className={style.statistic}>
                <TotalStatistic latestTotals={props.countries.latestTotals}/>
                <DailyStatistic latestTotals={props.countries.latestTotals}/>
            </div>
            <div className={style.empty}></div>
            <GraphStatisticContainer country={props.countries.country}/>
        </div>
    )
}

export default CountriesStatistic;