import React from 'react';
import {connect} from "react-redux";
import GraphStatistic from "./GraphStatistic";
import {clearGraphicData, getGraphicData} from "../../../redux/graphic-reducer";
import {dateFunction} from "../../../utils/dateFunction";

class GraphGetStatisticContainer extends React.Component {

    refreshGraphData() {
        this.props.clearGraphicData();
        let arrayOfDates = dateFunction();
        this.props.getGraphicData(this.props.country, arrayOfDates);
    }

    componentDidMount() {
        this.refreshGraphData();
        window.addEventListener('unhandledrejection', this.catchAllUnhandledErrors)
    }

    componentDidUpdate(prevProps, prevState, snapshot) {
        if (this.props.country !== prevProps.country) {
            this.refreshGraphData();
        }
    }

    componentWillUnmount() {
        this.props.clearGraphicData();
        window.removeEventListener('unhandledrejection', this.catchAllUnhandledErrors)
    }


    render() {
        return (
            <div>
                <GraphStatistic data={this.props.data}/>
            </div>
        )
    }
}

const mapStateToProps = (state) => ({
    data: state.graphic
})

const GraphStatisticContainer = connect(mapStateToProps, {getGraphicData, clearGraphicData})(GraphGetStatisticContainer)

export default GraphStatisticContainer
