import React, {Component} from 'react';
import './App.css';
import Header from "./Components/Header/Header";
import Footer from "./Components/Footer/Footer";
import {connect} from "react-redux";
import {initializeApp} from "./redux/app-reducer";
import Preloader from "./Components/common/Preloader/Preloader";
import {Redirect, Route} from "react-router-dom";
import CountriesStatisticContainer from "./Components/Countries/CountriesStatisticContainer";
import MainBodyContainer from "./Components/MainBody/MainBodyContainer";

class App extends Component {

    componentDidMount() {
        this.props.initializeApp();
    }

    render() {
        if (!this.props.initialized) {
            <Preloader/>
        }

        return (
            <div className="App">
                <Header/>
                <div className="wrapper">
                    <Route path='/' render={() => <Redirect to={'/world'} />}/>
                    <Route path='/world' render={() => <MainBodyContainer/>}/>
                    <Route path='/countries' render={() => <CountriesStatisticContainer/>}/>

                </div>
                <Footer/>
            </div>
        );
    }
}
const mapStateToProps = (state) => ({
    initialized: state.app.initialized
})


const AppContainer = connect(mapStateToProps, {initializeApp})(App);

export default AppContainer;
