import React from 'react';
import {connect} from "react-redux";
import MainBody from "./MainBody";
import {getLatestTotals, setCountry} from "../../redux/country-reducer";
import {getCurrentDate} from "../../utils/dateFunction";

class MainBodyGetTotalContainer extends React.Component {

    refreshCountry() {
        this.props.getLatestTotals(this.props.countries.allWorld, getCurrentDate());
    }

    componentDidMount() {
        this.refreshCountry();
    }

    componentDidUpdate(prevProps, prevState, snapshot) {
        if(this.props.countries.allWorld !== prevProps.countries.allWorld) {
            this.refreshCountry();
        }
    }

    componentWillUnmount() {

    }

    render() {
        return (
            <div>
                <MainBody countries={this.props.countries}/>
            </div>
        )
    }
}

const mapStateToProps = (state) => ({
    countries: state.countries
})


const MainBodyContainer = connect(mapStateToProps, {getLatestTotals, setCountry})(MainBodyGetTotalContainer)

export default MainBodyContainer;