import React from 'react';
import {Area, AreaChart, CartesianGrid, Legend, ResponsiveContainer, Tooltip, XAxis, YAxis} from "recharts";
import style from './GraphStatistic.module.css'
import Preloader from "../../common/Preloader/Preloader";


const GraphStatistic = (props) => {

    if (!props.data.success) {
        <Preloader />
    }

    return(
        <div className={style.graph}>

            <h2 className={style.headText}>Graphic Statistic</h2>
            <ResponsiveContainer width="100%" height={500}>
            <AreaChart key={props.data.data} data={props.data.data}
                       margin={{ top: 50, right: 100, left: 100, bottom: 50 }}>
                <defs>
                    <linearGradient id="colorUv" x1="0" y1="0" x2="0" y2="1">
                        <stop offset="5%" stopColor="#0a0fff" stopOpacity={0.8}/>
                        <stop offset="95%" stopColor="#0a0fff" stopOpacity={0}/>
                    </linearGradient>
                    <linearGradient id="colorPv" x1="0" y1="0" x2="0" y2="1">
                        <stop offset="5%" stopColor="#ff762c" stopOpacity={0.8}/>
                        <stop offset="95%" stopColor="#ff762c" stopOpacity={0}/>
                    </linearGradient>
                    <linearGradient id="colorDv" x1="0" y1="0" x2="0" y2="1">
                        <stop offset="5%" stopColor="#000" stopOpacity={0.8}/>
                        <stop offset="95%" stopColor="#000" stopOpacity={0}/>
                    </linearGradient>
                </defs>
                <XAxis dataKey="name" />
                <YAxis />
                <CartesianGrid strokeDasharray="3 3" />
                <Legend wrapperStyle={{bottom: 20 }}/>
                <Tooltip />
                <Area type="monotone" dataKey="cases" stroke="#0a0fff" fillOpacity={1} fill="url(#colorUv)" />
                <Area type="monotone" dataKey="recovered" stroke="#ff762c" fillOpacity={1} fill="url(#colorPv)" />
                <Area type="monotone" dataKey="deaths" stroke="#000" fillOpacity={1} fill="url(#colorDv)" />
            </AreaChart>
            </ResponsiveContainer>
        </div>
    )
}

export default GraphStatistic;