import React, {useEffect, useState} from "react";
import style from './Search.module.css';
import searchImage from './../../../img/searchIcon.ico'

let listItems, countriesArray;

const SearchCountriesField = (props) => {

    const [searchText, setSearchText] = useState('');
    const [styleDisplay, setStyleDisplay] = useState('none');
    const [disableBtn, setDisableBtn] = useState(true);

    useEffect(() => {
        getStyleDisplay();
    })

    const searchFunction = (input) => {
        let filter = input.value.toUpperCase();
        let li = document.querySelectorAll('.list')[0].querySelectorAll('li');

        for (let i = 0; i < li.length; i++){
            if(li[i].innerHTML.toUpperCase().indexOf(filter) > -1){
                li[i].style.display = '';
            }else {
                li[i].style.display = 'none';
            }
        }
    }

    const setOnFocus = () => {
        countriesArray = props.countriesList;
        listItems = countriesArray.map((c, index) =>
            <li key={index} onClick={onListClick}>{c}</li>
        )

    }

    const getStyleDisplay = () => {
        document.querySelectorAll('.list')[0].style.display = styleDisplay;
    }

    const onTextChange = (e) => {
        setStyleDisplay('block');
        setSearchText(e.target.value);
        searchFunction(e.target);
    }

    const searchListHide = () => {
        setStyleDisplay('none');
    }

    const onListClick = (e) => {
        let chooseCountry = e.target.innerHTML;
        searchListHide();
        setSearchText(chooseCountry);
        setDisableBtn(false)
    }

    const onSetCountry = () => {
        props.setCountry(searchText);
        setSearchText('');
        setDisableBtn(true);
    }

    return <div className={style.searchField}>
        <div>
            <input className={style.input} type={'text'} placeholder={'choose the country'}
               onFocus={setOnFocus} value={searchText} onChange={onTextChange}/>
               <button className={style.searchBtn} disabled={disableBtn} onClick={onSetCountry}><img src={searchImage} alt={'O'}/></button>
        </div>
        <div className={style.dropList} id="dropList">
            <ul className='list'>{listItems}</ul>
        </div>
    </div>
}

export default SearchCountriesField;