import React from 'react';
import style from './MainBody.module.css'
import GraphStatisticContainer from "./Graphics/GraphStatisticContainer";
import TotalStatistic from "./TotalStatistics/TotalStatistic";
import DailyStatistic from "./DailyStatistic/DailyStatistic";

const MainBody = (props) => {
    return(
        <div className={style.mainBody}>
            <div className={style.headText}><h2>All World</h2></div>
            <div className={style.statistic}>
                <TotalStatistic latestTotals={props.countries.latestTotals}/>
                <DailyStatistic latestTotals={props.countries.latestTotals} />
            </div>
            <div className={style.empty}></div>
            <GraphStatisticContainer country={props.countries.allWorld}/>
        </div>
    )
}

export default MainBody;