import React from 'react';
import style from './TotalStatistic.module.css'

const TotalStatistic = (props) => {

    return(
        <div className={style.mainField}>
            <div className={style.field}>
                <h2>From the beginning of pandemia</h2>
                <div className={style.totalCases}>Total Cases: {props.latestTotals.cases.total}</div>
                <div className={style.recovered}>Recovered: {props.latestTotals.cases.recovered}</div>
                <div className={style.deaths}>Deaths: {props.latestTotals.deaths.total}</div>
            </div>
        </div>
    )
}

export default TotalStatistic;