import React from 'react'
import {connect} from "react-redux";
import CountriesStatistic from "./CountriesStatistic";
import {getLatestTotals, setCountry} from "../../redux/country-reducer";
import {getCurrentDate} from "../../utils/dateFunction";

class GetCountryStatisticContainer extends React.Component {

    refreshCountry() {
        let date = getCurrentDate();
        this.props.getLatestTotals(this.props.countries.country, date)
    }

    componentDidMount() {
        this.refreshCountry();
    }

    componentDidUpdate(prevProps, prevState, snapshot) {
        if(this.props.countries.country !== prevProps.countries.country) {
            this.refreshCountry();
        }
    }

    componentWillUnmount() {
    }

    render() {
        return <div>
            <CountriesStatistic countries={this.props.countries} setCountry={this.props.setCountry}/>
        </div>
    }
}

const mapStateToProps = (state) => ({
    countries: state.countries
})

const CountriesStatisticContainer = connect(mapStateToProps, {setCountry, getLatestTotals})(GetCountryStatisticContainer)

export default CountriesStatisticContainer;