import * as axios from "axios"

const instance = axios.create({
    baseURL: 'https://covid-193.p.rapidapi.com/',
    withCredentials: true,
    headers: {
        'x-rapidapi-key': 'd6f449e374msh95fbebc4c03b5fdp15b26ejsn9238dc1aa6d7',
        'x-rapidapi-host': 'covid-193.p.rapidapi.com',
        "useQueryString": true
    }
})

export const countriesAPI = {
    getListOfCountries() {
        return instance.get(`countries`).then(response => {
            return response.data.response
        })
    }
}

export const totalApi = {
    getLatestTotals(country, date) {
        return instance.get(`history`, {params: {country: country, day: date}}).then(response => {
            return response.data
        })
    }
}
